<script setup lang="ts">
const route = useRoute();
const { locale } = useI18n();
const runtimeConfig = useRuntimeConfig().public;

const { data: page } = await useFetch<IDirectusPage>(`/json/customer/${route.params.type}.json`);
const defaultLanguage = runtimeConfig.APP_CUSTOMER.default_language;

const pageContent = computed(() => {
  const content = page.value?.translations?.find(t => t.languages_code === locale.value)?.page_content
    || page.value?.translations?.find(t => t.languages_code === defaultLanguage)?.page_content
    || page.value?.translations?.[0]?.page_content;

  return content;
});

useHead({ title: page.value?.title });
definePageMeta({ layout: "home" });
</script>

<template>
  <div class="main-container">
    <article v-if="pageContent" class="prose dark:prose-invert max-w-none" v-html="pageContent" />
    <div v-else>
      <BaseError />
    </div>
  </div>
</template>
